.Input {
    width: 100%;
    padding: 10px;
    box-sizing: border-box;
}

.Label {
    font-weight: bold;
    display: block;
    margin: 8px;
}

.InputElement {
    outline: none;
    border: 1px solid #ccc;
    background-color: white;
    font: inherit;
    padding: 6px 10px;
    display: block;
    width: 100%;
    box-sizing: border-box;

}

.InputElement:focus {
    outline: none;
    background-color: #ccc;

}

.Invalid {
    border: 1px solid red;
    background-color: #FDA49A;

}
.Auth {
    margin: 20px auto;
    width: 90%;
    text-align: center;
    background-color: white;
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.12),
    0 2px 4px 0 rgba(0,0,0,0.08);
    border-radius: 10px;
    padding: 20px;
    box-sizing: border-box; 
}

.lower-button {
    margin-top: 20px;
    margin-bottom: 20px
}

@media (min-width: 600px) {
    .Auth {
        width: 500px;
    }
}
